import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { discountedPrice, ITEMS_PER_PAGE } from '../../../app/constants';
import { fetchAllOrdersAsync, selectOrders, selectTotalOrders, updateOrderAsync } from '../../order/orderSlice';
import { EyeIcon, PencilIcon, ArrowDownIcon, ArrowUpIcon } from '@heroicons/react/20/solid'
import Pagination from '../../common/Pagination';

function AdminOrder() {

  const dispatch = useDispatch();
  const [page, setPage] = useState(1);
  const orders = useSelector(selectOrders);
  const totalOrders = useSelector(selectTotalOrders)
  const [editableOrderId, setEditableOrderId] = useState(-1);
  const [sort, setSort] = useState({});

  const chooseColor = (status) => {
    switch (status) {
      case 'pending':
        return 'bg-purple-200 text-purple-600';
      case 'dispatched':
        return 'bg-yellow-200 text-yellow-600';
      case 'delivered':
        return 'bg-green-200 text-green-600';
      case 'canceled':
        return 'bg-red-200 text-red-600';
      default:
        return 'bg-purple-200 text-purple-600';
    }
  }

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    dispatch(fetchAllOrdersAsync({pagination, sort}));
  }, [dispatch, page, sort])


  const handleSort = (option) => {
    setPage(1);
    const newSort = { _sort:option.sort, _order:option.order };
    setSort(newSort)
  };


  const handleShow = (order) => {
    console.log(order);
  }

  const handleEdit = (order) => {
    console.log(order);
    setEditableOrderId(order.id);
  }

  const handleUpdate = (e, order) => {
    const updatedOrder = { ...order, status: e.target.value };
    dispatch(updateOrderAsync(updatedOrder));
    setEditableOrderId(-1);
  }

  const handlePage = (page) => {
    setPage(page);
  }

  return (
    <>
      {/* component */}
      <div className="overflow-x-auto">
        <div className="min-w-screen min-h-scree flex items-center justify-center font-sans overflow-hidden">
          <div className="w-full">
            <div className="bg-white shadow-md rounded my-6">
              <table className="min-w-max w-full table-auto">
                <thead>
                  <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                    <th className="py-3 px-6 text-left cursor-pointer" onClick={
                      () => handleSort({sort:'id', order:sort._order==='asc'?'desc':'asc'})
                    }>Order#
                    {sort._sort==="id" && (sort._order==="asc" ? <ArrowUpIcon className="h-4 w-4 inline ml-1"/> : <ArrowDownIcon className="h-4 w-4 ml-1 inline"/>)}
                    </th>
                    <th className="py-3 px-6 text-left">Items</th>
                    <th className="py-3 px-6 text-center cursor-pointer" onClick={
                      () => handleSort({sort:'totalAmount', order:sort._order==='asc'?'desc':'asc'})
                    }>Total Amount
                     {sort._sort==="totalAmount" && (sort._order==="asc" ? <ArrowUpIcon className="h-4 w-4 inline ml-1"/> : <ArrowDownIcon className="h-4 w-4 ml-1 inline"/>)}
                   </th>
                    <th className="py-3 px-6 text-center">Shipping Address</th>
                    <th className="py-3 px-6 text-center">Status</th>
                    <th className="py-3 px-6 text-center">Actions</th>
                  </tr>
                </thead>
                <tbody className="text-gray-600 text-sm font-light">
                  {orders.map((order) => (<tr className="border-b border-gray-200 hover:bg-gray-100">
                    <td className="py-3 px-6 text-left whitespace-nowrap">
                      <div>
                        <span className="font-medium">{order.id}</span>
                      </div>
                    </td>
                    <td className="py-3 px-6 text-left">
                      {order.items.map((item) => (<div className="flex items-center">
                        <div className="mr-2">
                          <img
                            className="w-6 h-6 rounded-full"
                            src={item.product.thumbnail}
                          />
                        </div>
                        <span>{item.product.title} - #{item.quantity} - ${discountedPrice(item)}</span>
                      </div>))}
                    </td>
                    <td className="py-3 px-6 text-center font-medium">
                      ${order.totalAmount}
                    </td>
                    <td className="py-3 px-6 text-center">

                      <div className='font-medium'>{order.selectedAddress.name}</div>
                      <div>{order.selectedAddress.email}</div>
                      <div>{order.selectedAddress.phone}</div>
                      <div>{order.selectedAddress.street}</div>
                      <div>{order.selectedAddress.city}</div>
                      <div>{order.selectedAddress.state}</div>
                      <div>{order.selectedAddress.zipCode}</div>

                    </td>
                    <td className="py-3 px-6 text-center">
                    {order.id === editableOrderId ? (
                      <select className="select select-bordered rounded-md w-full max-w-xs" onChange={(e)=>handleUpdate(e, order)}>
                        <option value="pending">Pending</option>
                        <option value="dispatched">Dispatched</option>
                        <option value="delivered">Delivered</option>
                        <option value="canceled">Canceled</option>
                      </select>
                    ):(
                      <span className={`${chooseColor(order.status)} py-1 px-3 rounded-full text-xs`}>
                        {order.status}
                      </span>
                    )}
                     
                    </td>
                    <td className="py-3 px-6 text-center">
                      <div className="flex item-center justify-center">
                        <div className="w-4 mr-2 transform hover:text-purple-500 hover:scale-110">
                          <EyeIcon onClick={() => handleShow(order)} />
                        </div>
                        <div className="w-4 mr-2 transform hover:text-purple-500 hover:scale-110">
                          <PencilIcon onClick={() => handleEdit(order)} />
                        </div>
                      </div>
                    </td>
                  </tr>))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <Pagination totalItems={totalOrders} page={page} handlePage={handlePage} />
      </div>
    </>

  )
}

export default AdminOrder