import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectLoggedInUserToken } from '../../auth/authSlice';
import { fetchLoggedInUserOrdersAsync, selectUserInfo, selectUserOrders } from '../userSlice';


export default function UserOrders() {

  const dispatch = useDispatch();
  const userInfo = useSelector(selectLoggedInUserToken)
  const orders = useSelector(selectUserOrders)

  useEffect(() => {
    dispatch(fetchLoggedInUserOrdersAsync())
  }
    , [dispatch, userInfo])

  return (
    <div>
      {
        orders.map((order) => (
          <>
            <div className="mx-auto mt-12 bg-white max-w-7xl px-4 sm:px-6 lg:px-8">
              <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                <h1 className="text-4xl my-5 font-bold tracking-tight text-gray-900">
                  Order #{order.id}
                </h1>
                <h4 className="text-xl my-5 font-bold tracking-tight text-gray-900">
                  Status : {" "}
                  <span className="rounded-md bg-red-50 px-2 py-1 text-sm font-medium text-red-700 ring-1 ring-inset ring-red-600/10">
                    {order.status}
      </span>
                </h4>
                <div className="flow-root">
                  <ul role="list" className="-my-6 divide-y divide-gray-200">
                    {order.items.map((item) => (
                      <li key={item.id} className="flex py-6">
                        <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                          <img
                            src={item.product.thumbnail}
                            alt={item.product.title}
                            className="h-full w-full object-cover object-center"
                          />
                        </div>

                        <div className="ml-4 flex flex-1 flex-col">
                          <div>
                            <div className="flex justify-between text-base font-medium text-gray-900">
                              <h3>
                                <a href={item.product.thumbnail}>{item.product.title}</a>
                              </h3>
                              <p className="ml-4">${Math.round(item.product.price * (1 - item.product.discountPercentage / 100))}</p>
                            </div>
                            <p className="mt-1 text-sm text-gray-500">
                              {item.product.brand}
                            </p>
                          </div>
                          <div className="flex flex-1 items-end justify-between text-sm">
                            <div className="text-gray-500">
                              <label
                                htmlFor="quantity"
                                className="inline mr-5 text-sm font-medium leading-6 text-gray-900"
                              >
                                Qty : 
                              </label>
                              <span
                              className="text-sm font-medium leading-6 text-gray-900"
                               >{item.quantity}</span>
                            </div>


                          </div>
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              </div>

              <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                <div className="flex justify-between text-base font-medium text-gray-900">
                  <p>Subtotal</p>
                  <p>${order.totalAmount}</p>
                </div>
                <div className="flex justify-between text-base font-medium text-gray-900">
                  <p>Total Items in Cart</p>
                  <p>{order.totalQuantity} items</p>
                </div>

                <div className="text-base font-medium text-gray-900 mt-5">
                  <p>Shipping Address :</p>
                  <div className="flex justify-between gap-x-6 py-5 border p-5 mt-2">
                    <div className="flex min-w-0 gap-x-4">
                      <div className="min-w-0 flex-auto">
                        <p className="font-semibold leading-6 text-gray-900">{order.selectedAddress.name}</p>
                        <p className="mt-1 truncate leading-5 text-gray-500">{order.selectedAddress.street}</p>
                        <p className="mt-1 truncate leading-5 text-gray-500">{order.selectedAddress.city}</p>
                        <p className="mt-1 truncate leading-5 text-gray-500">{order.selectedAddress.zipCode}</p>
                      </div>
                    </div>
                    <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                      <p className="leading-6 text-gray-900">{order.selectedAddress.state}</p>
                      <p className="mt-1 truncate leading-5 text-gray-500">{order.selectedAddress.phone}</p>
                    </div>
                  </div>
                </div>



              </div>
            </div>
          </>
        ))
      }
    </div>
  );
}
