import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectUserInfo, updateUserAsync } from '../userSlice';


export default function UserProfile() {

  const dispatch = useDispatch();
  const userInfo = useSelector(selectUserInfo)
  const [editModes, setEditModes] = useState(Array(userInfo.addresses.length).fill(false));
  const [openNewAddressForm, setOpenNewAddressForm] = useState(false);
  const [newAddress, setNewAddress] = useState({
    name: '',
    street: '',
    city: '',
    state: '',
    zipCode: '',
    phone: '',
    email: '',
  });

  const handleChange = (event) => {
    setNewAddress({ ...newAddress, [event.target.name]: event.target.value })
  };

  const handleOpenNewAddressForm = () => {
    setNewAddress({
      name: '',
      street: '',
      city: '',
      state: '',
      zipCode: '',
      phone: '',
      email: '',
    })
    setOpenNewAddressForm(true);
    const updatedEditModes = [...editModes].fill(false);
    setEditModes(updatedEditModes);
  }

  const handleAddNewAddress = () => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses, newAddress] }
    dispatch(updateUserAsync(newUser))
    setOpenNewAddressForm(false);
  }

  const handleRemove = (e, id) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] }
    newUser.addresses.splice(id, 1)
    dispatch(updateUserAsync(newUser))
  }

  const handleEditButton = (index) => {
    const updatedEditModes = [...editModes].fill(false);
    updatedEditModes[index] = true;
    setEditModes(updatedEditModes);
    let newEditedAddress = userInfo.addresses[index]
    setNewAddress(newEditedAddress);
  };

  const handleCancel = (index) => {
    console.log(index);
    const updatedEditModes = [...editModes];
    updatedEditModes[index] = false;
    setEditModes(updatedEditModes);
  };

  const handleSaveChanges = (index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] }
    newUser.addresses.splice(index, 1, newAddress)
    dispatch(updateUserAsync(newUser))
    const updatedEditModes = [...editModes].fill(false);
    setEditModes(updatedEditModes);
  };



  return (
    <>

      <div className="mx-auto mt-12 bg-white max-w-7xl px-4 sm:px-6 lg:px-8">

        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
          <h1 className="text-4xl my-5 font-bold tracking-tight text-gray-900">
            Name: {userInfo.name ? userInfo.name : "N/A"}
          </h1>
          <h4 className="text-xl my-5 font-bold tracking-tight text-gray-900">
            Email : {userInfo.email ? userInfo.email : "N/A"}
          </h4>
        </div>

        <div className="border-gray-200 px-4 pb-6 sm:px-6">
          {openNewAddressForm ? (<div className=''>
            <div className="grid grid-cols-1 gap-y-6 gap-x-4 sm:grid-cols-6">
              <div className="sm:col-span-2">
                <label
                  htmlFor="name"
                  className="block text-sm font-medium text-gray-700"
                >
                  Name
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="name"
                    id="newName"
                    required
                    value={newAddress.name}
                    onChange={(e) => handleChange(e)}
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
              <div className="sm:col-span-2">
                <label
                  htmlFor="street"
                  className="block text-sm font-medium text-gray-700"
                >
                  Street
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="street"
                    id="newStreet"
                    required
                    value={newAddress.street}
                    onChange={(e) => handleChange(e)}
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
              <div className="sm:col-span-2">
                <label
                  htmlFor="city"
                  className="block text-sm font-medium text-gray-700"
                >
                  City
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="city"
                    id="newCity"
                    required
                    value={newAddress.city}
                    onChange={(e) => handleChange(e)}
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
            </div>
            <div className="grid grid-cols-1 gap-y-6 gap-x-4 mt-4 sm:grid-cols-8">
              <div className="sm:col-span-2">
                <label
                  htmlFor="state"
                  className="block text-sm font-medium text-gray-700"
                >
                  State
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="state"
                    id="newState"
                    required
                    value={newAddress.state}
                    onChange={(e) => handleChange(e)}
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
              <div className="sm:col-span-2">
                <label
                  htmlFor="zipCode"
                  className="block text-sm font-medium text-gray-700"
                >
                  Zip Code
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="zipCode"
                    id="newZipCode"
                    required
                    value={newAddress.zipCode}
                    onChange={(e) => handleChange(e)}
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
              <div className="sm:col-span-2">
                <label
                  htmlFor="phone"
                  className="block text-sm font-medium text-gray-700"
                >
                  Phone
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="phone"
                    id="newPhone"
                    required
                    value={newAddress.phone}
                    onChange={(e) => handleChange(e)}
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
              <div className="sm:col-span-2">
                <label
                  htmlFor="email"
                  className="block text-sm font-medium text-gray-700"
                >
                  Email
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="email"
                    id="newEmail"
                    required
                    value={newAddress.email}
                    onChange={(e) => handleChange(e)}
                    className="shadow-sm focus:ring-indigo-500 focus:border-indigo-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
            </div>
            <div className="grid grid-cols-1gap-y-6 gap-x-4 sm:grid-cols-1 pt-6">
            <div className='mx-auto'>
            <button
                type="button"
                onClick={() => handleAddNewAddress()}
                className="rounded-md mx-2 bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Add New Address
              </button>
              <button
                type="button"
                onClick={() => setOpenNewAddressForm(false)}
                className="rounded-md bg-gray-200 px-3 py-2 text-sm font-semibold text-black shadow-sm hover:bg-gray-300 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Cancel
              </button>
            </div>
            
            </div>
          </div>) : (
            <>
              <button
                type="button"
                onClick={() => handleOpenNewAddressForm()}
                className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
              >
                Add New Address
              </button>
            </>
          )}
        </div>


        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

          <div className="text-base font-medium text-gray-900 mt-5">
            <p>Your Addresses :</p>
            {/* <ul role="list" className="divide-y divide-gray-100">
              {user.addresses.map((address, index) => (
                <li key={index} className="flex justify-between gap-x-6 py-5">
                  <div className="flex min-w-0 gap-x-4">
                    <input
                      id="address"
                      name="address"
                      type="radio"
                      value={index}
                      onClick={handleSelectedAddress}
                      className="h-4 w-4 mt-1 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                    />
                    <div className="min-w-0 flex-auto">
                      <p className=" font-semibold leading-6 text-gray-900">{address.name}</p>
                      <p className="mt-1 truncate leading-5 text-gray-500">{address.street}</p>
                      <p className="mt-1 truncate leading-5 text-gray-500">{address.city}</p>
                      <p className="mt-1 truncate leading-5 text-gray-500">{address.zipCode}</p>
                    </div>
                  </div>
                  <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                    <p className=" leading-6 text-gray-900">{address.state}</p>
                    <p className="mt-1 truncate leading-5 text-gray-500">{address.phone}</p>
                  </div>
                  <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                  <button
                            onClick={(e)=>{handleEdit(e, index)}}
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                          >
                            Edit
                          </button>
                          <button
                            onClick={(e)=>{handleRemove(e, index)}}
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                          >
                            Remove
                          </button>
                  </div>
                </li>
              ))}
            </ul> */}

            {/* New Ul List */}
            <ul role="list" className="divide-y divide-gray-100">
              {userInfo.addresses.map((address, index) => (
                <li key={index} className="flex justify-between gap-x-6 py-5">
                  <div className="flex min-w-0 gap-x-4">
                    <div className="min-w-0 flex-auto">
                      {editModes[index] ? (
                        <>
                          <input
                            type="text"
                            name='name'
                            value={newAddress.name}
                            className="block w-full my-2 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            onChange={(e) => handleChange(e)}
                          />
                          <input
                            type="text"
                            name='street'
                            value={newAddress.street}
                            className="block w-full my-2 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            onChange={(e) => handleChange(e)}
                          />
                          <input
                            type="text"
                            name='city'
                            value={newAddress.city}
                            className="block w-full my-2 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            onChange={(e) => handleChange(e)}
                          />
                          <input
                            type="text"
                            name='zipCode'
                            value={newAddress.zipCode}
                            className="block w-full my-2 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                            onChange={(e) => handleChange(e)}
                          />
                          {/* Add input fields for other address properties */}
                        </>
                      ) : (
                        <>
                          <p className=" font-semibold leading-6 text-gray-900">{address.name}</p>
                          <p className="mt-1 truncate leading-5 text-gray-500">{address.street}</p>
                          <p className="mt-1 truncate leading-5 text-gray-500">{address.city}</p>
                          <p className="mt-1 truncate leading-5 text-gray-500">{address.zipCode}</p>
                        </>
                      )}
                    </div>
                  </div>
                  <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                    {editModes[index] ? (
                      <>
                        <input
                          type="text"
                          name='state'
                          value={newAddress.state}
                          className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          onChange={(e) => handleChange(e)}
                        />
                        <input
                          type="text"
                          name='phone'
                          value={newAddress.phone}
                          className="block w-full my-2 rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          onChange={(e) => handleChange(e)}
                        />
                        <input
                          type="text"
                          name='email'
                          value={newAddress.email}
                          className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          onChange={(e) => handleChange(e)}
                        />
                      </>
                    ) : (
                      <>
                        <p className=" leading-6 text-gray-900">{address.state}</p>
                        <p className="mt-1 truncate leading-5 text-gray-500">{address.phone}</p>
                        <p className="mt-1 truncate leading-5 text-gray-500">{address.email}</p>
                      </>
                    )}
                  </div>

                  <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                    {editModes[index] ? (
                      <>
                        <button
                          onClick={() => handleSaveChanges(index)}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Save Changes
                        </button>
                        <button
                          onClick={() => handleCancel(index)}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Cancel
                        </button>
                      </>
                    ) : (
                      <>
                        <button
                          onClick={() => handleEditButton(index)}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Edit
                        </button>
                        <button
                          onClick={(e) => handleRemove(e, index)}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Remove
                        </button>
                      </>
                    )}
                  </div>
                </li>
              ))}
            </ul>
            {/* End of new Ul List */}
          </div>



        </div>
      </div>


    </>
  );
}
