import React, { useState } from 'react'
import { PhotoIcon, UserCircleIcon } from '@heroicons/react/24/solid'
import { Link, Navigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { deleteItemFromCartAsync, selectItems, updateCartIdAsync } from '../features/cart/cartSlice';
import { useForm } from 'react-hook-form';
import { createOrderAsync, selectCurrentOrder } from '../features/order/orderSlice';
import { selectUserInfo, updateUserAsync } from '../features/user/userSlice';

function Checkout() {
    const dispatch = useDispatch();
    const [open, setOpen] = useState(true);
    const items = useSelector(selectItems);
    const totalAmount = items.reduce((sum, item) => sum + (Math.round(item.product.price * (1 - item.product.discountPercentage / 100)) * item.quantity), 0);
    const totalQuantity = items.reduce((sum, item) => sum + item.quantity, 0);
    const user = useSelector(selectUserInfo);
    const { register, handleSubmit, watch, reset, formState: { errors } } = useForm();
    const [selectedAddress, setSelectedAddress] = useState(null);
    const [selectedPayment, setSelectedPayment] = useState("cash");
    const currentOrder = useSelector(selectCurrentOrder);

    const handleQuantity = (e, item) => {
        dispatch(updateCartIdAsync({ id:item.id,quantity: +e.target.value }))
    }

    const removeItem = (e, id) => {
        dispatch(deleteItemFromCartAsync(id))
    }

    const handleSelectedAddress = (e) => {
        const address = user.addresses[e.target.value]
        setSelectedAddress(address)
    }

    const handlePayment = (e) => {
        setSelectedPayment(e.target.value)
    }

    const handleOrder = (e) => {
        e.preventDefault()
        if (selectedAddress && selectedPayment) {
            const order = {items, selectedAddress, paymentMethod:selectedPayment,user:user.id, totalAmount, totalQuantity, status: "pending"}
            dispatch(createOrderAsync(order))
        }
    }

    return (
        <>
        {currentOrder && currentOrder.paymentMethod === "cash" && <Navigate to={`/order-success/${currentOrder.id}`}></Navigate>}
        {currentOrder && currentOrder.paymentMethod === "card" && <Navigate to={`/payment-card-checkout`}></Navigate>}
        <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
            <div className="grid grid-cols-1 gap-x-8 gap-y-10 lg:grid-cols-5">
                <div className="lg:col-span-3">
                    <form className="bg-white px-4 py-4 lg:mt-12" onSubmit={
                        handleSubmit((data) => {
                            dispatch(updateUserAsync({...user, addresses: [...user.addresses, data]}))
                            reset()
                        })
                    }>
                        <div className="space-y-12">
                            <div className="border-b border-gray-900/10 pb-12">
                                <h2 className="text-base font-semibold leading-7 text-gray-900">Personal Information</h2>
                                <p className="mt-1 text-sm leading-6 text-gray-600">Use a permanent address where you can receive mail.</p>

                                <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                                    <div className="sm:col-span-4">
                                        <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                                            Full name
                                        </label>
                                        <div className="mt-2">
                                            <input
                                                type="text"
                                                {...register("name", { required: "Please fill the name field." })}
                                                id="first-name"
                                                autoComplete="given-name"
                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                            />
                                        </div>
                                        {errors.name && <span className="text-red-500 text-xs">{errors.name.message}</span>}
                                    </div>

                                    <div className="sm:col-span-4">
                                        <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                                            Email address
                                        </label>
                                        <div className="mt-2">
                                            <input
                                                id="email"
                                                {...register("email", {
                                                    required: "Please fill the email field.", pattern: {
                                                        value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/i,
                                                        message: "invalid email address"
                                                    }
                                                })}
                                                type="email"
                                                autoComplete="email"
                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                            />
                                        </div>
                                        {errors.email && <span className="text-red-500 text-xs">{errors.email.message}</span>}
                                    </div>

                                    <div className="sm:col-span-4">
                                        <label htmlFor="phone" className="block text-sm font-medium leading-6 text-gray-900">
                                            phone
                                        </label>
                                        <div className="mt-2">
                                            <input
                                                id="phone"
                                                {...register("phone", {
                                                    required: "Please fill the phone field.", pattern: {
                                                        value: /^[0-9]{10}$/,
                                                        message: "invalid phone number"
                                                    }
                                                })}
                                                type="text"
                                                autoComplete="phone"
                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                            />
                                        </div>
                                        {errors.phone && <span className="text-red-500 text-xs">{errors.phone.message}</span>}
                                    </div>

                                    <div className="col-span-full">
                                        <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
                                            Street address
                                        </label>
                                        <div className="mt-2">
                                            <input
                                                type="text"
                                                {...register("street", { required: "Please fill the street field." })}
                                                id="street"
                                                autoComplete="street"
                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                            />
                                        </div>
                                        {errors.street && <span className="text-red-500 text-xs">{errors.street.message}</span>}
                                    </div>

                                    <div className="sm:col-span-2 sm:col-start-1">
                                        <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                                            City
                                        </label>
                                        <div className="mt-2">
                                            <input
                                                type="text"
                                                {...register("city", { required: "Please fill the city field." })}
                                                id="city"
                                                autoComplete="address-level2"
                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                            />
                                        </div>
                                        {errors.city && <span className="text-red-500 text-xs">{errors.city.message}</span>}
                                    </div>

                                    <div className="sm:col-span-2">
                                        <label htmlFor="state" className="block text-sm font-medium leading-6 text-gray-900">
                                            State / Province
                                        </label>
                                        <div className="mt-2">
                                            <input
                                                type="text"
                                                {...register("state", { required: "Please fill the state field." })}
                                                id="state"
                                                autoComplete="address-level1"
                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                            />
                                        </div>
                                    </div>

                                    <div className="sm:col-span-2">
                                        <label htmlFor="zipCode" className="block text-sm font-medium leading-6 text-gray-900">
                                            ZIP / Postal code
                                        </label>
                                        <div className="mt-2">
                                            <input
                                                type="text"
                                                {...register("zipCode", { required: "Please fill the zipCode field." })}
                                                id="zipCode"
                                                autoComplete="zipCode"
                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                            />
                                        </div>
                                        {errors.zipCode && <span className="text-red-500 text-xs">{errors.zipCode.message}</span>}
                                    </div>
                                </div>

                            </div>
                            <div className="mt-2 flex items-center justify-end gap-x-6">
                                <button type="button" className="text-sm font-semibold leading-6 text-gray-900" onClick={()=>{reset()}}>
                                    Reset
                                </button>
                                <button
                                    type="submit"
                                    className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                                >
                                    Add Address
                                </button>
                            </div>


                            <div className="border-b border-gray-900/10 pb-12">
                                <h2 className="text-base font-semibold leading-7 text-gray-900">Address</h2>
                                <p className="mt-1 text-sm leading-6 text-gray-600">
                                    Select the address where you want your order to be shipped.
                                </p>

                                <ul role="list" className="divide-y divide-gray-100">
                                    {user.addresses.map((address, index) => (
                                        <li key={index} className="flex justify-between gap-x-6 py-5">
                                            <div className="flex min-w-0 gap-x-4">
                                                <input
                                                    id="address"
                                                    name="address"
                                                    type="radio"
                                                    value={index}
                                                    onClick={handleSelectedAddress}
                                                    className="h-4 w-4 mt-1 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                                                />
                                                <div className="min-w-0 flex-auto">
                                                    <p className="text-sm font-semibold leading-6 text-gray-900">{address.name}</p>
                                                    <p className="mt-1 truncate text-xs leading-5 text-gray-500">{address.street}</p>
                                                    <p className="mt-1 truncate text-xs leading-5 text-gray-500">{address.city}</p>
                                                    <p className="mt-1 truncate text-xs leading-5 text-gray-500">{address.zipCode}</p>
                                                </div>
                                            </div>
                                            <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                                                <p className="text-sm leading-6 text-gray-900">{address.state}</p>
                                                <p className="mt-1 truncate text-xs leading-5 text-gray-500">{address.phone}</p>

                                            </div>
                                        </li>
                                    ))}
                                </ul>

                                <div className="mt-10 space-y-10">
                                    <fieldset>
                                        <legend className="text-sm font-semibold leading-6 text-gray-900">Pay Options</legend>
                                        <p className="mt-1 text-sm leading-6 text-gray-600">Choose your preferred payment methods.</p>
                                        <div className="mt-6 space-y-6">
                                            <div className="flex items-center gap-x-3">
                                                <input
                                                    id="cash"
                                                    name="payments"
                                                    type="radio"
                                                    value="cash"
                                                    onClick={handlePayment}
                                                    checked={selectedPayment === "cash"}
                                                    className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                                                />
                                                <label htmlFor="push-everything" className="block text-sm font-medium leading-6 text-gray-900">
                                                    Cash on Delivery
                                                </label>
                                            </div>
                                            <div className="flex items-center gap-x-3">
                                                <input
                                                    id="card"
                                                    name="payments"
                                                    type="radio"
                                                    value="card"
                                                    onClick={handlePayment}
                                                    checked={selectedPayment === "card"}
                                                    className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                                                />
                                                <label htmlFor="push-email" className="block text-sm font-medium leading-6 text-gray-900">
                                                    Debit Card / Credit Card / Net Banking
                                                </label>
                                            </div>

                                        </div>
                                    </fieldset>
                                </div>
                            </div>
                        </div>


                    </form>
                </div>
                <div className="lg:col-span-2">
                    <div className="mx-auto mt-12 bg-white max-w-7xl px-2 sm:px-0 lg:px-0">
                        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                            <h6 className="text-2xl my-5 font-bold tracking-tight text-gray-900">
                                Order Summary
                            </h6>
                            <div className="flow-root">
                                <ul role="list" className="-my-6 divide-y divide-gray-200">
                                    {items.map((item) => (
                                        <li key={item.id} className="flex py-6">
                                            <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                                <img
                                                    src={item.product.thumbnail}
                                                    alt={item.product.title}
                                                    className="h-full w-full object-cover object-center"
                                                />
                                            </div>

                                            <div className="ml-4 flex flex-1 flex-col">
                                                <div>
                                                    <div className="flex justify-between text-base font-medium text-gray-900">
                                                        <h3>
                                                            <a href={item.product.thumbnail}>{item.product.title}</a>
                                                        </h3>
                                                        <p className="ml-4">${Math.round(item.product.price * (1 - item.product.discountPercentage / 100))}</p>
                                                    </div>
                                                    <p className="mt-1 text-sm text-gray-500">
                                                        {item.product.brand}
                                                    </p>
                                                </div>
                                                <div className="flex flex-1 items-end justify-between text-sm">
                                                    <div className="text-gray-500">
                                                        <label
                                                            htmlFor="quantity"
                                                            className="inline mr-5 text-sm font-medium leading-6 text-gray-900"
                                                        >
                                                            Qty
                                                        </label>
                                                        <select className="rounded-md" onChange={(e) => {
                                                            handleQuantity(e, item)
                                                        }} value={item.quantity}>
                                                            <option value="1">1</option>
                                                            <option value="2">2</option>
                                                            <option value="3">3</option>
                                                            <option value="4">4</option>
                                                            <option value="5">5</option>
                                                            <option value="6">6</option>
                                                        </select>
                                                    </div>

                                                    <div className="flex">
                                                        <button
                                                            onClick={(e) => { removeItem(e, item.id) }}
                                                            type="button"
                                                            className="font-medium text-indigo-600 hover:text-indigo-500"
                                                        >
                                                            Remove
                                                        </button>
                                                    </div>
                                                </div>
                                            </div>
                                        </li>
                                    ))}
                                </ul>
                            </div>
                        </div>

                        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                            <div className="flex justify-between text-base font-medium text-gray-900">
                                <p>Subtotal</p>
                                <p>${totalAmount}</p>
                            </div>
                            <div className="flex justify-between text-base font-medium text-gray-900">
                                <p>Total Items in Cart</p>
                                <p>{totalQuantity} items</p>
                            </div>
                            <p className="mt-0.5 text-sm text-gray-500">
                                Shipping and taxes calculated at checkout.
                            </p>
                            <div className="mt-6">
                                <div
                                    onClick={(e)=>{handleOrder(e)}}
                                    className="flex items-center cursor-pointer justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                                >
                                    Order Now
                                </div>
                            </div>
                            <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                                <p>
                                    or{" "}
                                    <Link to="/">
                                        <button
                                            type="button"
                                            className="font-medium text-indigo-600 hover:text-indigo-500"
                                            onClick={() => setOpen(false)}
                                        >
                                            Continue Shopping
                                            <span aria-hidden="true"> &rarr;</span>
                                        </button>
                                    </Link>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}

export default Checkout